import { useRef } from "react";
import Hidinglist from "../Hidinglist";

const Oxy = ({page}) => {

    const main = useRef();
    const lines = useRef();
    const areas = useRef();
    const templates = useRef();

    return page(
    <div>
        <h1 className="main-menu" ref={main} onClick={page.index}>  {`\\(Oxy\\)`}</h1>
        <ul className="list">
            <li onClick={page.scroll(lines)}>    </li>
            <li onClick={page.scroll(areas)}>    </li>
            <li onClick={page.scroll(templates)}>    </li>
        </ul>
        <h2 onClick={page.scroll(main)} ref={lines} className="main-menu"> </h2>
        <ol>
            <li>
                <div className="frame">
                    <div>: {`\\(~~~\\alpha x+ \\beta y+\\gamma=0\\)`}</div>
                </div>
            </li>
            <li>
                <div className="frame">
                    <div> : {`\\(~~~y = \\alpha(x-\\beta)+\\gamma\\)`}</div>
                    <div>
                        <Description
                            block={
                                <ul className="margin-list">
                                    <li>,  {`\\((\\beta;~\\gamma)\\)`}, {`\\(\\alpha~-\\)`}  .</li>
                                </ul>
                            }
                        />
                    </div>
                </div>
            </li>
            <li>
                <div className="frame">
                    <div> : {`\\(~~~x=\\alpha\\)`}</div>
                    <div>
                        <Description
                            block={
                                <ul>
                                    <li> ,  {`\\((\\alpha;~0)\\)`}.</li>
                                </ul>  
                            }
                        />
                    </div>
                </div>
            </li>
            <li>
                <div className="frame">
                    <div>
                          :  {`\\(~~~y = \\alpha(x-\\beta)^2 + \\gamma\\)`}.
                    </div>
                    <div>
                        <Description
                            block={
                                <ul>
                                    <li> {`\\(\\alpha = 0\\)`}   {`\\(y=\\gamma\\)`}.</li>
                                    <li> {`\\(\\alpha = 0\\)`} ,  {`\\((\\beta;~\\gamma)\\)`},  {`\\(\\alpha\\)`}   .</li>
                                </ul>
                            }
                        />
                    </div>
                    
                </div>
            </li>
            <li>
                <div className="frame">
                    <div>
                             : {`\\(~~~(x-\\alpha)^2+(y-\\beta)^2=\\gamma\\)`}
                    </div>
                    <Description
                    block={
                        <ul>
                            <li> {`\\(\\gamma > 0\\)`} ,  {`\\((\\alpha;~\\beta)\\)`},  {`\\(\\sqrt{\\gamma}\\)`}.</li>        
                            <li> {`\\(\\gamma = 0\\)`}  {`\\((\\alpha;~\\beta)\\)`}.</li>
                            <li> {`\\(\\gamma < 0\\)`}  .</li>
                        </ul>
                    }
                    />
                </div>
            </li>
            <li>
                <div className="frame">
                    <div>
                        : {`\\(~~~y=\\alpha |x-\\beta| + \\gamma\\)`}
                    </div>
                    <Description
                        block={
                            <ul>
                                <li>
                                    ,  {`\\((\\beta;~\\gamma)\\)`},  
                                    {`\\(~y=\\alpha x,~y=-\\alpha x\\)`},     {`\\(\\alpha\\)`}.
                                </li>
                                <li>
                                      : {`\\(~~~\\cases{y=-\\alpha (x-\\beta) + \\gamma \\\\ x \\leqslant \\beta} \\)`}.
                                </li>
                                <li>
                                      : {`\\(~~~\\cases{y=\\alpha (x-\\beta) + \\gamma \\\\ x \\geqslant \\beta} \\)`}.
                                </li>
                            </ul>
                        }
                    />
                </div>
            </li>
            <li>
                <div className="frame">
                    <div>
                          : {`\\(~~~y = \\displaystyle\\frac{\\alpha}{x - \\beta} + \\gamma\\)`}
                    </div>
                    <Description
                        block={
                            <ul>
                                <li>
                                     {`\\(\\alpha \\neq 0\\)`} ,  {`\\((\\beta;~\\gamma)\\)`},  {`\\(\\alpha\\)`}    .
                                </li>
                                <li>
                                     {`\\(\\alpha = 0\\)`}   {`\\(y=\\gamma\\)`}   {`\\((\\beta;~\\gamma)\\)`}.
                                </li>
                            </ul>
                        }
                    />
                </div>
            </li>
        </ol>
        <h2 onClick={page.scroll(main)} ref={areas} className="main-menu"> </h2>
        <ol>
            <li>
                <div className="frame">
                    <div>{`\\(y \\vee f(x)~~~-\\)`}   /   {`\\(y = f(x)\\)`} ( /  ).</div>
                    <Cases
                        block={
                            <ul>
                                <li>{`\\(y > f(x)~~~-\\)`}    {`\\(y=f(x)\\)`}.</li>
                                <li>{`\\(y \\leqslant f(x)~~~-\\)`}    {`\\(y=f(x)\\)`} ().</li>
                                <li>{`\\(x \\geqslant f(y)~~~-\\)`}    {`\\(x=f(y)\\)`} ().</li>
                                <li>{`\\(x < f(y)~~~-\\)`}     {`\\(x=f(y)\\)`}.</li>
                                <li>{`\\(f(y) \\leqslant x < g(y)~~~-\\)`}    {`\\(x=f(y)\\)`} ()  {`\\(x=g(y)\\)`}.</li>
                            </ul>
                        }
                    />
                </div>
            </li>
            <li>
                <div className="frame">
                    <div>{`\\(f(x,y) \\neq 0~~~-\\)`}    {`\\(f(x,y)=0\\)`}.</div>
                    <div><i>:</i>      {`\\(f(x,y)\\)`}.</div>
                </div>
            </li>
            <li>
                <div className="frame">
                    <div>{`\\(f(x,y) \\vee 0~~~-\\)`} ,  {`\\(f(x,y) = 0\\)`} ( /  ).</div>
                    <div>     ,     ,</div>
                    <div>     .   {`\\(\\vee~ 0\\)`},</div>
                    <div>  ,    , ,  .</div>
                    <div><i>:</i>      {`\\(f(x,y)\\)`}.</div>
                </div>
            </li>
        </ol>
        <h2 className="main-menu" onClick={page.scroll(main)} ref={templates}>   </h2>
        <ol>
            <li>
                <div className="frame">
                    <div> {`\\(G(x,y)\\)`}    {`\\(A\\)`}: {`\\(~~~A \\in G(x,y)\\)`}</div>
                    <Then block={`\\(G(A)\\)`} />
                </div>
            </li>
            <li>
                <div className="frame">
                    <div>    {`\\(G(x,y)\\)`}  {`\\(F(x,y)\\)`}</div>
                    <Then block={`\\(\\cases{G(x,y) \\\\ F(x,y)}\\)`} />
                </div>
            </li>
            <li>
                <div className="frame">
                    <div>
                         {`\\(y = \\alpha_1(x-\\beta_1) + \\gamma_1~~\\)`}  
                        {`\\(~~y = \\alpha_2(x-\\beta_2) + \\gamma_2~~\\)`}    
                    </div>
                    <Then block={`\\(\\alpha_1 = \\alpha_2\\)`}/>
                </div>
            </li>
            <li>
                <div className="frame">
                    <div>  {`\\(y=f(x)\\)`}   {`\\(y=g(x)\\)`}</div>
                    <Then block={`\\(\\cases{f(x) = g(x) \\\\ f'(x) = g'(x)}\\)`}/>
                </div>
            </li>
            <li>
                <div className="frame">
                    <div>
                          {`\\(l~\\)`}
                            {`\\(C\\)`}   {`\\(r\\)`}
                    </div>
                    <Then block={`\\(\\rho\\left(C, l\\right) = r\\)`}/>
                </div>
            </li>
            <li>
                <div className="frame">
                    <div>    {`\\((C_1,r_1)\\)`}  {`\\((C_2,r_2)\\)`}</div>
                    <Then block={`\\(\\rho(C_1,C_2) = r_1 + r_2\\)`}/>
                </div>
            </li>
            <li>
                <div className="frame">
                    <div>    {`\\((C_1,r_1)\\)`}  {`\\((C_2,r_2)\\)`}</div>
                    <Then block={`\\(\\rho(C_1,C_2) = |r_1 - r_2|\\)`}/>
                </div>
            </li>
            <li>
                <div className="frame">
                    <div style={{width:'500px'}}>     </div>
                    <Formula
                        block={
                            <>
                                <div>{`\\(\\rho(A,B) = \\sqrt{(A_x - B_x)^2+(A_y - B_y)^2}\\)`}</div>
                                <div style={{marginTop:'50px'}}>
                                    &laquo;   {`\\(A\\)`}   {`\\(B\\)`}&raquo; &nbsp;&nbsp; &copy; Oxxxymiron
                                </div>
                            </>
                        }
                    />
                </div>
            </li>
            <li>
                <div className="frame">
                    <div>     </div>
                    <Formula block={`\\(\\rho(A,~~ \\alpha x + \\beta y + \\gamma = 0) = \\displaystyle\\frac{|\\alpha A_x + \\beta A_y + \\gamma|}{\\sqrt{\\alpha^2 + \\beta^2}}\\)`}/>
                </div>
            </li>
        </ol>
        <div style={{height:'500px'}}></div>
    </div>
);
}

const Cases = ({block}) => <Showsmth smth=' ' block={block} />

const Description = ({block}) => (
    <Showsmth smth={''} block={block} />
);

const Then = ({block}) => (
    <Showsmth smth={''} block={block} />
);

const Formula = ({block}) => <Showsmth smth={''} block={block} />

const Showsmth = ({block, smth}) => (
    <Hidinglist
        parent={<div className="sub-menu"> {smth}</div>}
        parent1={<div className="sub-menu"> {smth}</div>}
        child={<div>{block}</div>}
    />
);

export default Oxy;