import Hidinglist from "./Hidinglist";


const Test = ({page}) => page(
    <div>
        <Hidinglist
        parent={<div>123123412</div>}
                            child={<div>
                            <div>:</div>
                            <div>
                                <div className="flex line">
                                    <div>{`\\((i):~~\\)`}</div>
                                    <div>
                                        <div>{'\\(\\infty\\)'}   {`\\(a \\in A\\)`}</div>
                                        <div>0   {`\\(a \\not\\in A\\)`}</div>
                                    </div>
                                </div>
                                <div>     .</div>
                            </div>
                            </div>}
                            />
        
    </div>
);
export default Test;